import { BrowserRouter as Router, Routes, Route, Outlet } from 'react-router-dom';

import Layout from '../Layout/Layout';
import NotFound from '../Templates/NotFound/NotFound';
import Home from '../Templates/Home/Home';
import Login from '../Templates/Login/Login';
import DemoData from '../Templates/DemoData/DemoData';
import Connections from '../Templates/Connections/Connections';

import './App.css';
import { HelmetProvider } from 'react-helmet-async';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import Profile from '../Templates/Profile/Profile';
import Blog from '../Templates/Blog/Blog';
import UiDemo from '../Templates/DemoData/UiDemo';
import ProfileOverview from '../Templates/Profile/Overview';
import BlogDetail from '../Templates/Blog/BlogDetail';

const queryClient = new QueryClient();

function App() {
  return (
    <HelmetProvider>
      <QueryClientProvider client={queryClient}>
        <Router>
          <Routes>
            <Route path="/owner/login" element={<Login />}></Route>
            <Route
              path="/owner"
              element={
                <Layout>
                  <Outlet />
                </Layout>
              }
            >
              <Route index={true} element={<Home />} />
              <Route path="demo-data" element={<DemoData />}></Route>
              <Route path="ui-demo" element={<UiDemo />}></Route>
              <Route path="profile" element={<ProfileOverview />}></Route>
              <Route path="profile/:profileKey" element={<Profile />}></Route>
              <Route path="connections" element={<Connections />}></Route>
              <Route path="blog" element={<Blog />}></Route>
              <Route path="blog/:channelKey" element={<Blog />}></Route>
              <Route path="blog/:channelKey/:blogKey" element={<BlogDetail />}></Route>
            </Route>

            <Route
              path="*"
              element={
                <Layout>
                  <NotFound />
                </Layout>
              }
            />
          </Routes>
        </Router>
      </QueryClientProvider>
    </HelmetProvider>
  );
}

export default App;
