import { AttributeFile } from '@youfoundation/dotyoucore-transit-lib';
import { t } from '../../../../helpers/i18n/dictionary';
import useAttribute from '../../../../hooks/profiles/useAttribute';
import { AttributeVm } from '../../../../hooks/profiles/useAttributes';
import ActionButton from '../../../Buttons/ActionButton';
import Section from '../../../Sections/Section';
import AclEditor from './AclEditor';
import AttributeFields from './AttributeFields';

const AttributeEditor = ({
  attribute,
  className,
}: {
  attribute: AttributeVm;
  className?: string;
}) => {
  const [, { mutate: saveAttr, isLoading, isError, isSuccess }] = useAttribute({});
  const newData = { ...attribute.data };

  const saveNewData = () => {
    saveAttr({ ...attribute, data: newData } as AttributeFile);
  };

  const changeHandler: React.ChangeEventHandler<HTMLInputElement | HTMLTextAreaElement> = (e) => {
    newData[e.target.name] = e.target.value;

    saveNewData();
  };

  return (
    <Section
      isOpaqueBg={true}
      title={
        <>
          <AclEditor acl={attribute.acl} /> {attribute.typeDefinition.name}
        </>
      }
      className={`${className} relative`}
    >
      <AttributeFields attribute={attribute} onChange={changeHandler} />
      <div className="top-5 right-5 flex flex-row md:absolute">
        <ActionButton type="remove" className="ml-auto">
          {t('Remove')}
        </ActionButton>
        <ActionButton
          state={isLoading ? 'loading' : isSuccess ? 'success' : isError ? 'failed' : 'success'}
          type="primary"
          className="ml-2"
          onClick={saveNewData}
        >
          {t('Save')}
        </ActionButton>
      </div>
    </Section>
  );
};

export default AttributeEditor;
