import { useQuery } from '@tanstack/react-query';
import {
  ApiType,
  BlogContent,
  BlogPostPublishStatus,
  Client,
} from '@youfoundation/dotyoucore-transit-lib';
import { convertTextToSlug } from '../../helpers/common';
import useAuth from '../auth/useAuth';

type useBlogPostsProps = {
  channelId?: string;
  page: number;
};

export interface BlogContentVm extends BlogContent {
  itemKey?: string;
}

const useBlogs = ({ channelId, page }: useBlogPostsProps = { page: 1 }) => {
  const pageSize = 50;
  const { getSharedSecret } = useAuth();

  const fetchBlogData = async ({ channelId, page }: useBlogPostsProps = { page: 1 }) => {
    const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });

    let foundBlogPosts: BlogContentVm[];

    // If located in a specific channel, only fetch published in those, otherwise fallback to drafts;
    if (channelId) {
      //client.blogPostProvider.getPosts(BlogPostPublishStatus.Published);
      foundBlogPosts = await client.blogPostReadonlyProvider.getPosts(
        channelId,
        'Article',
        page,
        pageSize
      );
    } else {
      foundBlogPosts = (
        await client.blogPostProvider.getPosts(BlogPostPublishStatus.Draft, page, pageSize)
      ).map((blogFile) => blogFile.content);
    }

    // Process blog posts (Only until fixed in data upload)
    foundBlogPosts = foundBlogPosts
      .filter((item) => item.id)
      .map((item) => {
        return {
          ...item,
          slug: item.caption ? convertTextToSlug(item.caption) : item.id,
          itemKey: `${item.channelId}_${item.id}_page-${page}`,
        };
      });

    return foundBlogPosts;
  };

  return useQuery(['blogs', channelId, page], () => fetchBlogData({ channelId, page }), {
    refetchOnMount: false,
    refetchOnWindowFocus: false,
  });
};

export default useBlogs;
