import {
  ApiType,
  AttributeDefinitions,
  BuiltInProfiles,
  Client,
} from '@youfoundation/dotyoucore-transit-lib';
import useAuth from '../../hooks/auth/useAuth';

const DemoButtons = () => {
  const { getSharedSecret } = useAuth();

  const testConnection = async () => {
    const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });

    const nameAttr = await client.profileDataProvider.getBestAttributeVersion(
      BuiltInProfiles.StandardProfileId.toString(),
      BuiltInProfiles.PersonalInfoSectionId.toString(),
      AttributeDefinitions.Name.type.toString()
    );

    console.log(nameAttr);
  };

  return (
    <>
      <h1 className="text-xl">Dashboard</h1>
      <div className="-mx-5 mt-5 flex flex-wrap">
        <div className="px-5">
          <a
            onClick={testConnection}
            className="block rounded border-0 bg-green-500 py-2 px-4 text-white hover:bg-green-600 focus:outline-none"
          >
            Test 1
          </a>
        </div>
        <div className="px-5">
          <a
            onClick={testConnection}
            className="block rounded border-0 bg-green-500 py-2 px-4 text-white hover:bg-green-600 focus:outline-none"
          >
            Test 2
          </a>
        </div>
        <div className="px-5">
          <a
            onClick={testConnection}
            className="block rounded border-0 bg-green-500 py-2 px-4 text-white hover:bg-green-600 focus:outline-none"
          >
            Test 3
          </a>
        </div>
        <div className="px-5">
          <a
            onClick={testConnection}
            className="block rounded border-0 bg-green-500 py-2 px-4 text-white hover:bg-green-600 focus:outline-none"
          >
            Test 4
          </a>
        </div>
      </div>
    </>
  );
};

export default DemoButtons;
