import {
  BlogArticle,
  BlogContent,
  BlogPostFile,
  SecurityGroupType,
} from '@youfoundation/dotyoucore-transit-lib';
import { debounce } from 'lodash-es';
import { useMemo, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { Descendant } from 'slate';
import { convertTextToSlug } from '../../../helpers/common';
import { t } from '../../../helpers/i18n/dictionary';
import useBlog from '../../../hooks/blogs/useBlog';
import useChannels from '../../../hooks/blogs/useChannels';
import ActionButton from '../../Buttons/ActionButton';
import Input from '../../Form/Input';
import Textarea from '../../Form/Textarea';
import RichTextEditor from '../../RichTextEditor/RichTextEditor';

const BlogDetail = () => {
  const navigate = useNavigate();
  const { channelKey, blogKey } = useParams();
  const [{ data: channels, isLoading: isChannelsLoading }] = useChannels();
  const activeChannel = channels?.find((channel) => channel.slug === channelKey);

  const [{ data: blog, isLoading: isBlogLoading }, { mutate: saveBlog, status: saveBlogStatus }] =
    useBlog({
      blogSlug: blogKey,
    });

  const saveBlogFile = (blogFile: BlogPostFile<BlogContent>) => {
    saveBlog(blogFile, {
      onSuccess: (data) => {
        // If caption changed, then slug changes at the moment.. So we need to navigate
        // TODO Decide if we want to work with fileId, Content Id, Slugs
        // TODO When slug is stored as well, we still need to navigate, but when slug changes
        if (
          !blog ||
          (blogKey !== convertTextToSlug(blogFile.content.caption) &&
            blogKey !== blogFile.fileId?.toString())
        ) {
          const targetPath = blogFile.content.caption?.length
            ? convertTextToSlug(blogFile.content.caption)
            : data.toString();
          const path = window.location.pathname.split('/');
          path.pop();
          if (path && targetPath) {
            navigate(`${path.join('/')}/${targetPath}`);
          }
        }
      },
    });
  };

  if (!channels || isChannelsLoading || isBlogLoading) {
    return <>Loading</>;
  }

  return (
    <section>
      <div className="flex flex-row">
        <h1 className="mb-5 text-4xl">{blog ? blog.content.caption : t('New blog')}</h1>
        <ActionButton
          type="primary"
          state={saveBlogStatus === 'idle' ? 'success' : saveBlogStatus}
          className="my-auto ml-auto"
        >
          {saveBlogStatus === undefined || saveBlogStatus === 'idle' ? t('Save') : t('Saved')}
        </ActionButton>
      </div>
      {blog ? (
        <BlogEditor blog={blog} saveBlog={saveBlogFile} />
      ) : (
        <BlogArticleCreator activeChannelId={activeChannel?.channelId} saveBlog={saveBlogFile} />
      )}
    </section>
  );
};

const BlogEditor = ({
  blog,
  saveBlog,
}: {
  blog: BlogPostFile<BlogContent>;
  saveBlog: (blogFile: BlogPostFile<BlogContent>) => void;
}) => {
  const newData: BlogPostFile<BlogContent> = { ...blog };

  const onChange = (e: { target: { name: string; value: string | Descendant[] } }) => {
    if (Object.keys(newData.content).indexOf(e.target.name) !== -1) {
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      (newData.content as any)[e.target.name] = e.target.value;
    }

    saveBlog(newData);
  };

  return <BlogFields blog={blog.content} onChange={onChange} />;
};

const BlogArticleCreator = ({
  activeChannelId,
  saveBlog,
}: {
  activeChannelId?: string;
  saveBlog: (blogFile: BlogPostFile<BlogContent>) => void;
}) => {
  const [blog, setBlog] = useState<BlogPostFile<BlogArticle>>({
    acl: { requiredSecurityGroup: SecurityGroupType.Anonymous },
    publishTargets: [],
    content: {
      id: '',
      channelId: activeChannelId ?? '',
      caption: '',
      dateUnixTime: new Date().getTime(),
      type: 'Article',
      abstract: '',
      headerImageFileId: '',
      body: '',
    },
  });

  const onChange = (e: { target: { name: string; value: string | Descendant[] } }) => {
    const newBlog: BlogPostFile<BlogArticle> = { ...blog };

    if (newBlog.content[e.target.name as keyof BlogArticle] !== e.target.value) {
      if (Object.keys(newBlog.content).indexOf(e.target.name) !== -1) {
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        (newBlog.content as any)[e.target.name] = e.target.value;
      }

      setBlog(newBlog);
      saveBlog(newBlog);
    }
  };

  return <BlogFields blog={blog.content} onChange={onChange} />;
};

const BlogFields = ({
  blog,
  onChange,
}: {
  blog: BlogContent;
  onChange: (event: { target: { name: string; value: string | Descendant[] } }) => void;
}) => {
  const debouncedChange = useMemo(() => debounce(onChange, 500), [onChange]);

  const article: BlogArticle | undefined =
    blog.type === 'Article' ? (blog as BlogArticle) : undefined;

  const body = Array.isArray(article?.body)
    ? article?.body
    : [
        {
          type: 'paragraph',
          children: [{ text: article?.body ?? '' }],
        },
      ];

  return (
    <>
      <div className="mb-5">
        <label htmlFor="blog_caption">{t('Caption')}</label>
        <Input
          id="blog_caption"
          name="caption"
          defaultValue={blog.caption}
          onChange={debouncedChange}
        />
      </div>

      <div className="mb-5">
        <label htmlFor="blog_image">{t('Image File id [raw & readonly]')}</label>
        <Input
          id="blog_image"
          name="primaryImageFileId"
          defaultValue={blog.primaryImageFileId}
          disabled
        />
      </div>

      {blog.slug ? (
        <div className="mb-5">
          <label htmlFor="blog_slug">{t('Slug [raw & readonly]')}</label>
          <Input id="blog_slug" name="slug" defaultValue={blog.slug} disabled />
        </div>
      ) : null}

      {article ? (
        <>
          <div className="mb-5">
            <label htmlFor="blog_abstract">{t('Abstract')}</label>
            <Textarea
              id="blog_abstract"
              name="abstract"
              defaultValue={article.abstract}
              onChange={debouncedChange}
            />
          </div>

          <RichTextEditor
            defaultValue={body as Descendant[]}
            placeholder={t('Start writing...')}
            onChange={(value) => {
              onChange({ target: { name: 'body', value: value } });
            }}
          />
        </>
      ) : null}
    </>
  );
};

export default BlogDetail;
