import { useCallback, useMemo } from 'react';
import isHotkey from 'is-hotkey';
import { Editable, withReact, useSlate, Slate, ReactEditor, useSlateStatic } from 'slate-react';
import { Editor, Transforms, createEditor, Descendant, Element as SlateElement } from 'slate';
import { withHistory } from 'slate-history';

import { Button, Toolbar } from './RichTextEditorComponents';
import { ImageElement } from './rich-text-editor';
import { debounce } from 'lodash-es';

import Bold from '../Icons/RichTextEditorIcons/Bold/Bold';
import Italic from '../Icons/RichTextEditorIcons/Italic/Italic';
import Underline from '../Icons/RichTextEditorIcons/Underline/Underline';
import Quote from '../Icons/RichTextEditorIcons/Quote/Quote';
import Code from '../Icons/RichTextEditorIcons/Code/Code';
import Heading from '../Icons/RichTextEditorIcons/Heading/Heading';
import OrderedList from '../Icons/RichTextEditorIcons/OrderedList/OrderedList';
import UnorderedList from '../Icons/RichTextEditorIcons/UnorderedList/UnorderedList';
import Image from '../Icons/RichTextEditorIcons/Image/Image';

const HOTKEYS = {
  'mod+b': 'bold',
  'mod+i': 'italic',
  'mod+u': 'underline',
  'mod+`': 'code',
};

const LIST_TYPES = ['numbered-list', 'bulleted-list'];

const RichTextEditor = ({
  defaultValue,
  placeholder,
  onChange,
}: {
  defaultValue: Descendant[];
  placeholder: string;
  onChange: (value: Descendant[]) => void;
}) => {
  const renderElement = useCallback((props) => <Element {...props} />, []);
  const renderLeaf = useCallback((props) => <Leaf {...props} />, []);
  const editor = useMemo(() => withHistory(withReact(createEditor() as ReactEditor)), []);

  const debouncedChange = useMemo(() => debounce(onChange, 1500), [onChange]);

  return (
    <section className="w-[100%] overflow-hidden">
      <Slate editor={editor} value={defaultValue} onChange={debouncedChange}>
        <Toolbar>
          <MarkButton format="bold" icon={Bold} />
          <MarkButton format="italic" icon={Italic} />
          <MarkButton format="underline" icon={Underline} />
          <MarkButton format="code" icon={Code} />
          <BlockButton format="heading-one" icon={Heading} />
          <BlockButton format="heading-two" icon={Heading} />
          <BlockButton format="block-quote" icon={Quote} />
          <BlockButton format="numbered-list" icon={OrderedList} />
          <BlockButton format="bulleted-list" icon={UnorderedList} />
          <InsertImageButton />
        </Toolbar>
        <Editable
          renderElement={renderElement}
          renderLeaf={renderLeaf}
          placeholder={placeholder}
          spellCheck
          autoFocus
          onKeyDown={(event) => {
            for (const hotkey in HOTKEYS) {
              if (isHotkey(hotkey, event)) {
                event.preventDefault();
                const mark = HOTKEYS[hotkey];
                toggleMark(editor, mark);
              }
            }
          }}
        />
      </Slate>
    </section>
  );
};

const toggleBlock = (editor, format) => {
  const isActive = isBlockActive(editor, format, 'type');
  const isList = LIST_TYPES.includes(format);

  Transforms.unwrapNodes(editor, {
    match: (n) => !Editor.isEditor(n) && SlateElement.isElement(n) && LIST_TYPES.includes(n.type),
    split: true,
  });
  const newProperties: Partial<SlateElement> = {
    type: isActive ? 'paragraph' : isList ? 'list-item' : format,
  };

  Transforms.setNodes<SlateElement>(editor, newProperties);

  if (!isActive && isList) {
    const block = { type: format, children: [] };
    Transforms.wrapNodes(editor, block);
  }
};

const toggleMark = (editor, format) => {
  const isActive = isMarkActive(editor, format);

  if (isActive) {
    Editor.removeMark(editor, format);
  } else {
    Editor.addMark(editor, format, true);
  }
};

const isBlockActive = (editor, format, blockType = 'type') => {
  const { selection } = editor;
  if (!selection) return false;

  const [match] = Array.from(
    Editor.nodes(editor, {
      at: Editor.unhangRange(editor, selection),
      match: (n) => !Editor.isEditor(n) && SlateElement.isElement(n) && n[blockType] === format,
    })
  );

  return !!match;
};

const isMarkActive = (editor, format) => {
  const marks = Editor.marks(editor);
  return marks ? marks[format] === true : false;
};

const Element = ({ attributes, children, element }) => {
  const style = {};
  switch (element.type) {
    case 'block-quote':
      return (
        <blockquote style={style} {...attributes} className="border-l-4 pl-2">
          {children}
        </blockquote>
      );
    case 'bulleted-list':
      return (
        <ul style={style} {...attributes} className="list-disc pl-5">
          {children}
        </ul>
      );
    case 'heading-one':
      return (
        <h1 style={style} {...attributes} className={'text-2xl'}>
          {children}
        </h1>
      );
    case 'heading-two':
      return (
        <h2 style={style} {...attributes} className={'text-xl'}>
          {children}
        </h2>
      );
    case 'list-item':
      return (
        <li style={style} {...attributes}>
          {children}
        </li>
      );
    case 'numbered-list':
      return (
        <ol style={style} {...attributes} className="list-decimal pl-5">
          {children}
        </ol>
      );
    case 'image': {
      const src = element.url;
      return <img {...attributes} src={src} className="my-4 mx-auto" />;
    }
    default:
      return (
        <p style={style} {...attributes}>
          {children}
        </p>
      );
  }
};

const Leaf = ({ attributes, children, leaf }) => {
  if (leaf.bold) {
    children = <strong>{children}</strong>;
  }

  if (leaf.code) {
    children = <code>{children}</code>;
  }

  if (leaf.italic) {
    children = <em>{children}</em>;
  }

  if (leaf.underline) {
    children = <u>{children}</u>;
  }

  return <span {...attributes}>{children}</span>;
};

const BlockButton = ({ format, icon: Icon }) => {
  const editor = useSlate();
  return (
    <Button
      active={isBlockActive(editor, format, 'type')}
      onMouseDown={(event) => {
        event.preventDefault();
        toggleBlock(editor, format);
      }}
    >
      {Icon && <Icon className="h-5 w-5" />}
    </Button>
  );
};

const MarkButton = ({ format, icon: Icon }) => {
  const editor = useSlate();
  return (
    <Button
      active={isMarkActive(editor, format)}
      onMouseDown={(event) => {
        event.preventDefault();
        toggleMark(editor, format);
      }}
    >
      {Icon && <Icon className="h-5 w-5" />}
    </Button>
  );
};

const isImageUrl = (url: string) => {
  return true;
};

const insertImage = (editor, url) => {
  const text = { text: '' };
  const image: ImageElement = { type: 'image', url, children: [text] };
  Transforms.insertNodes(editor, image);
};

const InsertImageButton = () => {
  const editor = useSlateStatic();
  return (
    <Button
      onMouseDown={(event) => {
        event.preventDefault();
        const url = window.prompt('Enter the URL of the image:');
        if (url && !isImageUrl(url)) {
          alert('URL is not an image');
          return;
        }
        insertImage(editor, url);
      }}
    >
      <Image className="h-5 w-5" />
    </Button>
  );
};

