import { BlogArticle, ChannelDefinition } from '@youfoundation/dotyoucore-transit-lib';
import { useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { t } from '../../../helpers/i18n/dictionary';
import useBlogs, { BlogContentVm } from '../../../hooks/blogs/useBlogs';
import useChannels, { ChannelDefinitionVm } from '../../../hooks/blogs/useChannels';
import ActionButton from '../../Buttons/ActionButton';
import Input from '../../Form/Input';
import Plus from '../../Icons/Plus/Plus';
import Section from '../../Sections/Section';
import Submenu from '../../SubMenu/SubMenu';

const Blog = () => {
  const { channelKey } = useParams();
  const [{ data: channels, isLoading: isChannelsLoading }] = useChannels();
  const activeChannel = channels?.find((channel) => channel.slug === channelKey);
  const { data: blogs, isLoading: isBlogsLoading } = useBlogs({
    channelId: activeChannel?.channelId,
    page: 1,
  });

  if (!channels || !blogs || isChannelsLoading || isBlogsLoading) {
    return <>Loading</>;
  }

  return (
    <section>
      <div className="mb-5 flex flex-row">
        <h1 className="text-4xl">Blog</h1>
        <Link className="contents" to={`/owner/blog/draft/new`}>
          <ActionButton type="primary" icon="plus" className="ml-auto">
            {t('New')}
          </ActionButton>
        </Link>
      </div>

      <Submenu
        items={[
          {
            title: 'Drafts',
            key: '',
            path: `/owner/blog`,
          },
          ...channels.map((channel) => {
            return {
              title: channel.name,
              key: channel.channelId,
              path: `/owner/blog/${channel.slug}`,
            };
          }),
          {
            title: <Plus className="h-5 w-5" />,
            key: 'new',
            className: 'flex-grow-0',
            path: `/owner/blog/new`,
          },
        ]}
      />

      <div className="pt-5">
        {channelKey === 'new' ? (
          <ChannelCreator />
        ) : blogs.length ? (
          <ChannelEditor channel={activeChannel} blogs={blogs} />
        ) : (
          <>{t('no-data-found')}</>
        )}
      </div>
    </section>
  );
};

const ChannelCreator = () => {
  const [name, setName] = useState('');
  const [description, setDescription] = useState('');

  const createChannel: React.FormEventHandler<HTMLFormElement> = (e) => {
    e.preventDefault();

    const newChannelDef: ChannelDefinition = {
      channelId: '',
      name: name,
      description: description,
      templateId: undefined,
    };

    console.log('Should create: ', newChannelDef);

    return false;
  };

  return (
    <Section title="New: channel" isOpaqueBg={true}>
      <form onSubmit={createChannel}>
        <div className="mb-5">
          <label htmlFor="channelName">{t('Name')}</label>
          <Input
            id="name"
            name="channelName"
            onChange={(e) => {
              setName(e.target.value);
            }}
            required
          />
        </div>
        <div className="mb-5">
          <label htmlFor="name">{t('Description')}</label>
          <Input
            id="name"
            name="channelDescription"
            onChange={(e) => {
              setDescription(e.target.value);
            }}
            required
          />
        </div>
        <div className="flex flex-row">
          <ActionButton className="ml-auto">{t('Add channel')}</ActionButton>
        </div>
      </form>
    </Section>
  );
};

const ChannelEditor = ({
  channel,
  blogs,
}: {
  channel: ChannelDefinitionVm | undefined;
  blogs: BlogContentVm[];
}) => {
  const linkRoot = `/owner/blog/${channel ? channel.slug : 'all'}/`;

  return (
    <div className="-mx-5 divide-y-2 divide-gray-100 dark:divide-gray-800">
      {blogs.map((blog) => {
        return <BlogTeaser key={blog.itemKey} blog={blog} linkRoot={linkRoot} />;
      })}
    </div>
  );
};

const BlogTeaser = ({
  className,
  blog,
  linkRoot,
}: {
  className?: string;
  blog: BlogContentVm;
  linkRoot: string;
}) => {
  return (
    <div
      className={`flex flex-nowrap px-5 py-8 transition-colors duration-200 hover:bg-slate-50 hover:dark:bg-slate-800 ${
        className ?? ''
      }`}
    >
      <Link to={`${linkRoot}${blog.slug ?? '#'}`} className="contents">
        <div className="flex flex-col md:flex-grow md:flex-row">
          <div className="flex flex-shrink-0 md:order-3 md:mb-0 md:mt-3 md:w-32 md:flex-col md:text-right lg:w-64">
            <span className="title-font font-semibold text-gray-700 dark:text-white">
              {blog.type.toUpperCase()}
            </span>
            <span className="pl-2 text-gray-500 md:mt-1 md:pl-0 md:text-sm">
              {new Date(blog.dateUnixTime).toLocaleDateString()}
            </span>
          </div>
          <div className="md:order-2 md:flex-grow">
            <h2 className="title-font mb-2 text-2xl font-medium text-gray-900 dark:text-white">
              {blog.caption}
            </h2>
            {'abstract' in blog ? (
              <p className="leading-relaxed">{(blog as BlogArticle).abstract}</p>
            ) : null}
          </div>
        </div>
      </Link>
    </div>
  );
};

export default Blog;
