import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { ApiType, AttributeFile, Client } from '@youfoundation/dotyoucore-transit-lib';
import useAuth from '../auth/useAuth';

const useHomeAttributes = ({ attributeId }: { attributeId: string }) => {
  const queryClient = useQueryClient();

  const { getSharedSecret } = useAuth();
  const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });
  const fetchData = async (attributeId: string) => {
    const foundAttribute = await client.homePageProvider.getAttribute(attributeId);

    return foundAttribute;
  };

  const saveData = async (attribute: AttributeFile) => {
    await client.homePageProvider.saveAttribute(attribute);
  };

  return [
    useQuery(['homeAtrribute', attributeId], () => fetchData(attributeId), {
      refetchOnMount: false,
      refetchOnWindowFocus: false,
    }),
    useMutation(saveData, {
      onSuccess: (_data, variables) => {
        // Boom baby!
        if (variables.id) {
          queryClient.invalidateQueries(['homeAtrribute', variables.id]);
        } else {
          queryClient.invalidateQueries(['homeAtrribute']);
        }
      },
    }),
  ] as const;
};

export default useHomeAttributes;
