import useAuth from '../../../hooks/auth/useAuth';
import {
  ApiType,
  AttributeDefinitions,
  AttributeFile,
  BlogArticle,
  BlogContent,
  BlogPostFile,
  BlogPostPublishStatus,
  BuiltInProfiles,
  ChannelDefinition,
  Client,
  HomePageAttributes,
  HomePageConfig,
  HomePageFields,
  HomePageThemeFields,
  MinimalProfileFields,
  PublishTarget,
  SecurityGroupType,
  SocialProfileFields,
  TargetDrive,
  DataUtil,
  getTargetDriveFromProfileId,
  getBlogMasterContentTargetDrive,
} from '@youfoundation/dotyoucore-transit-lib';
import { demoImageArray } from './DemoImages';
import { base64ToArrayBuffer, getFunName, getRandomAbstract, rando } from './helpers';
import { lotrRealm } from './DemoLotr';
import useAttribute from '../../../hooks/profiles/useAttribute';
import useHomeAttributes from '../../../hooks/profiles/useHomeAttributes';
import useChannel from '../../../hooks/blogs/useChannel';

const character = window.location.hostname.indexOf('frodo') != -1 ? 'frodo' : 'sam';
const realmData = lotrRealm[character];

const DemoData = () => {
  const { getSharedSecret } = useAuth();
  const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });

  return (
    <section>
      <h1 className="text-2xl">Demo Data Generator: {character}</h1>
      <div className="my-5">
        <DemoDataProfile client={client} />
        <DemoDataHomeAndTheme client={client} />
        <DemoDataBlog client={client} />
      </div>
    </section>
  );
};

export default DemoData;

const uploadMedia = async (
  client: Client,
  imageData: { id: string; base64: string },
  targetDrive: TargetDrive
) => {
  const acl = {
    requiredSecurityGroup: SecurityGroupType.Anonymous,
  };

  const fileId = imageData.id;
  const imageArrayBuffer = base64ToArrayBuffer(imageData.base64);

  // Image uploads
  const newFileId = await client.mediaProvider.uploadImage(
    targetDrive,
    undefined,
    acl,
    new Uint8Array(imageArrayBuffer),
    fileId
  );

  if (newFileId?.toString() !== imageData.id) {
    console.warn('Image upload returned a different id than posted');
  }
  return newFileId;
};

const DemoDataProfile = ({ client }: { client: Client }) => {
  const profileId = BuiltInProfiles.StandardProfileId.toString();

  const [{ data: nameAttr, isFetched: isNameFetched }, { mutate: saveName }] = useAttribute({
    profileId: profileId,
    attributeId: realmData.name.id,
  });

  const addName = async () => {
    if (nameAttr) {
      return;
    }

    //set an attribute for the standard profile
    const newNameAttr: AttributeFile = {
      fileId: undefined,
      id: realmData.name.id,
      profileId: profileId,
      type: AttributeDefinitions.Name.type.toString(),
      priority: 100,
      sectionId: BuiltInProfiles.PersonalInfoSectionId.toString(),
      data: {},
      acl: { requiredSecurityGroup: SecurityGroupType.Anonymous },
    };

    newNameAttr.data[MinimalProfileFields.GiveNameId] = realmData.name.first;
    newNameAttr.data[MinimalProfileFields.SurnameId] = realmData.name.last;

    saveName(newNameAttr);

    return true;
  };

  const [{ data: photoAttr, isFetched: isPhotoFetched }, { mutate: savePhoto }] = useAttribute({
    profileId: profileId,
    attributeId: realmData.photo[0].id,
  });

  const addPhoto = async () => {
    if (photoAttr) {
      return;
    }

    const createProfilePhotoAttribute = async (
      id: string,
      media: { id: string; base64: string },
      securityGroup: SecurityGroupType
    ) => {
      const mediaFileId = await uploadMedia(client, media, getTargetDriveFromProfileId(profileId));

      // Look for existing attribute with this id:
      const foundAttribute = await client.profileDataProvider.getAttribute(
        BuiltInProfiles.StandardProfileId.toString(),
        id
      );

      if (foundAttribute) {
        console.warn('Already found a profile photo attribute with the demo id:', foundAttribute);
        return foundAttribute;
      }

      // Create attribute
      const anonymousPhotoAttribute: AttributeFile = {
        fileId: undefined,
        id: id,
        profileId: profileId,
        type: AttributeDefinitions.Photo.type.toString(),
        priority: 100,
        sectionId: BuiltInProfiles.PersonalInfoSectionId.toString(),
        data: {},
        acl: { requiredSecurityGroup: securityGroup },
      };

      anonymousPhotoAttribute.data[MinimalProfileFields.ProfileImageUrlId] =
        mediaFileId?.toString();

      savePhoto(anonymousPhotoAttribute);
      return true;
    };

    await Promise.all(
      realmData.photo.map(async (photo) => {
        await createProfilePhotoAttribute(photo.id, photo.image, photo.security);
      })
    );

    return true;
  };

  const [{ data: socialAttr, isFetched: isSocialFetched }, { mutate: saveSocial }] = useAttribute({
    profileId: profileId,
    attributeId: realmData.socials[0].id,
  });

  const addSocials = async () => {
    if (socialAttr) {
      return;
    }

    const createSocialAttribute = async (
      id: string,
      type: string,
      dataField: string,
      value: string
    ) => {
      // Search attribute:
      const foundAttribute = await client.profileDataProvider.getAttribute(profileId, id);

      if (foundAttribute) {
        console.warn(
          'Already found a profile social attribute (' + type + ') with the demo id:',
          foundAttribute
        );
        return;
      }

      // Create attribute:
      const socialAttribute: AttributeFile = {
        fileId: undefined,
        id: id,
        profileId: profileId,
        type: type,
        priority: 100,
        sectionId: BuiltInProfiles.SocialIdentitySectionId.toString(),
        data: {},
        acl: { requiredSecurityGroup: SecurityGroupType.Anonymous },
      };

      socialAttribute.data[dataField] = value;

      saveSocial(socialAttribute);

      return true;
    };

    const networkData = {
      Twitter: {
        attrDef: AttributeDefinitions.TwitterUsername.type.toString(),
        profileField: SocialProfileFields.Twitter,
      },
      Instagram: {
        attrDef: AttributeDefinitions.InstagramUsername.type.toString(),
        profileField: SocialProfileFields.Instagram,
      },
      Facebook: {
        attrDef: AttributeDefinitions.FacebookUsername.type.toString(),
        profileField: SocialProfileFields.Facebook,
      },
      LinkedIn: {
        attrDef: AttributeDefinitions.LinkedInUsername.type.toString(),
        profileField: SocialProfileFields.LinkedIn,
      },
      Tiktok: {
        attrDef: AttributeDefinitions.TiktokUsername.type.toString(),
        profileField: SocialProfileFields.Tiktok,
      },
    };

    type networkDataKey = keyof typeof networkData;

    await Promise.all(
      realmData.socials.map(async (social) => {
        const networkInfo = networkData[social.network as networkDataKey];

        await createSocialAttribute(
          social.id,
          networkInfo.attrDef,
          networkInfo.profileField,
          social.handle
        );
      })
    );

    return true;
  };

  const [{ data: bioAttr, isFetched: isBioFetched }, { mutate: saveBio }] = useAttribute({
    profileId: profileId,
    attributeId: realmData.bio[0].id,
  });

  const addBiography = async () => {
    const createBioAttribute = async (id: string, title: string, body: string) => {
      const foundAttribute = await client.profileDataProvider.getAttribute(profileId, id);

      if (foundAttribute) {
        console.warn('Already found a Bio Attribute with the demo id:', foundAttribute);
        return;
      }

      //set an attribute for the standard profile
      const bioAttr: AttributeFile = {
        fileId: undefined,
        id: id,
        profileId: profileId,
        type: AttributeDefinitions.FullBio.type.toString(),
        priority: 100,
        sectionId: BuiltInProfiles.PersonalInfoSectionId.toString(),
        data: {},
        acl: { requiredSecurityGroup: SecurityGroupType.Anonymous },
      };

      bioAttr.data[MinimalProfileFields.ShortBioId] = title;
      bioAttr.data[MinimalProfileFields.FullBioId] = body;

      saveBio(bioAttr);

      return true;
    };

    await Promise.all(
      realmData.bio.map(async (bio) => {
        await createBioAttribute(bio.id, bio.title, bio.body);
      })
    );

    return true;
  };

  const createProfile = async () => {
    await addName();
    await addPhoto();
    await addSocials();
    await addBiography();
  };

  return (
    <div className="mb-5">
      <h1>Profile:</h1>
      <button
        onClick={addName}
        className={`my-2 block w-1/3 rounded border-0  py-2 px-4 text-white hover:bg-green-600 focus:outline-none ${
          nameAttr && isNameFetched ? 'pointer-events-none bg-gray-300' : 'bg-green-500'
        }`}
        disabled={nameAttr && isNameFetched}
      >
        Add Name
      </button>
      <button
        onClick={addPhoto}
        className={`my-2 block w-1/3 rounded border-0  py-2 px-4 text-white hover:bg-green-600 focus:outline-none ${
          photoAttr && isPhotoFetched ? 'pointer-events-none bg-gray-300' : 'bg-green-500'
        }`}
        disabled={photoAttr && isPhotoFetched}
      >
        Add Photo
      </button>
      <button
        onClick={addSocials}
        className={`my-2 block w-1/3 rounded border-0  py-2 px-4 text-white hover:bg-green-600 focus:outline-none ${
          socialAttr && isSocialFetched ? 'pointer-events-none bg-gray-300' : 'bg-green-500'
        }`}
        disabled={socialAttr && isSocialFetched}
      >
        Add Socials
      </button>
      <button
        onClick={addBiography}
        className={`my-2 block w-1/3 rounded border-0  py-2 px-4 text-white hover:bg-green-600 focus:outline-none ${
          bioAttr && isBioFetched ? 'pointer-events-none bg-gray-300' : 'bg-green-500'
        }`}
        disabled={bioAttr && isBioFetched}
      >
        Add Bio
      </button>

      <button
        onClick={createProfile}
        className={`my-2 block w-1/3 rounded border-0 py-2 px-4 text-white hover:bg-orange-600 focus:outline-none ${
          nameAttr &&
          isNameFetched &&
          photoAttr &&
          isPhotoFetched &&
          socialAttr &&
          isSocialFetched &&
          bioAttr &&
          isBioFetched
            ? 'pointer-events-none bg-gray-300'
            : 'bg-grorangeeen-500'
        }`}
        disabled={
          nameAttr &&
          isNameFetched &&
          photoAttr &&
          isPhotoFetched &&
          socialAttr &&
          isSocialFetched &&
          bioAttr &&
          isBioFetched
        }
      >
        Create Profile All
      </button>
    </div>
  );
};

const DemoDataHomeAndTheme = ({ client }: { client: Client }) => {
  const [{ data: rootAttr, isFetched: isRootFetched }, { mutate: saveRoot }] = useHomeAttributes({
    attributeId: realmData.home.id,
  });

  const addHome = async () => {
    if (rootAttr) {
      return;
    }

    const homeId = realmData.home.id;
    const securityGroup = SecurityGroupType.Anonymous;

    // Create media
    const mediaFileId = await uploadMedia(
      client,
      realmData.home.headerImage,
      getTargetDriveFromProfileId(HomePageConfig.DefaultDriveId.toString())
    );

    // Create attribute
    const newRootAttr: AttributeFile = {
      fileId: undefined,
      id: homeId,
      profileId: HomePageConfig.DefaultDriveId.toString(),
      type: HomePageAttributes.HomePage.toString(),
      priority: -1,
      sectionId: HomePageConfig.AttributeSectionNotApplicable.toString(),
      data: {},
      acl: { requiredSecurityGroup: securityGroup },
    };

    newRootAttr.data[HomePageFields.HeaderImageId] = mediaFileId?.toString();
    newRootAttr.data[HomePageFields.TagLineId] = realmData.home.tagLine;
    newRootAttr.data[HomePageFields.LeadTextId] = realmData.home.lead;

    saveRoot(newRootAttr);

    return true;
  };

  const [{ data: themeAttr, isFetched: isThemeFetched }, { mutate: saveTheme }] = useHomeAttributes(
    {
      attributeId: realmData.theme.id,
    }
  );

  const addTheme = async () => {
    if (themeAttr) {
      return;
    }

    const themeId = realmData.theme.id;
    const securityGroup = SecurityGroupType.Anonymous;

    // Create attribute
    const anonymousHomeAttribute: AttributeFile = {
      fileId: undefined,
      id: themeId,
      profileId: HomePageConfig.DefaultDriveId.toString(),
      type: HomePageAttributes.Theme.toString(),
      priority: -1,
      sectionId: HomePageConfig.AttributeSectionNotApplicable.toString(),
      data: {},
      acl: { requiredSecurityGroup: securityGroup },
    };

    anonymousHomeAttribute.data[HomePageThemeFields.ThemeId] = realmData.theme.themeId;

    saveTheme(anonymousHomeAttribute);
  };

  const createHomeAndTheme = async () => {
    await addHome();
    await addTheme();
  };

  return (
    <div className="mb-5">
      <h1>Home and theme:</h1>
      <button
        onClick={addHome}
        className={`my-2 block w-1/3 rounded border-0  py-2 px-4 text-white hover:bg-green-600 focus:outline-none ${
          rootAttr && isRootFetched ? 'pointer-events-none bg-gray-300' : 'bg-green-500'
        }`}
        disabled={rootAttr && isRootFetched}
      >
        Add Home
      </button>
      <button
        onClick={addTheme}
        className={`my-2 block w-1/3 rounded border-0  py-2 px-4 text-white hover:bg-green-600 focus:outline-none ${
          themeAttr && isThemeFetched ? 'pointer-events-none bg-gray-300' : 'bg-green-500'
        }`}
        disabled={themeAttr && isThemeFetched}
      >
        Add Theme
      </button>

      <button
        onClick={createHomeAndTheme}
        className={`my-2 block w-1/3 rounded border-0  py-2 px-4 text-white hover:bg-orange-600 focus:outline-none ${
          rootAttr && isRootFetched && themeAttr && isThemeFetched
            ? 'pointer-events-none bg-gray-300'
            : 'bg-orange-500'
        }`}
        disabled={rootAttr && isRootFetched && themeAttr && isThemeFetched}
      >
        Create Home Page And Theme
      </button>
    </div>
  );
};

const DemoDataBlog = ({ client }: { client: Client }) => {
  const [{ data: channelAttr, isFetched: isChannelFetched }, { mutate: saveChannel }] = useChannel({
    channelId: realmData.blog.channels[0].id,
  });

  const addChannels = async () => {
    if (channelAttr) {
      return;
    }

    const addChannel = async (channelId: string, name: string, description: string) => {
      const foundDef = await client.blogDefinitionProvider.getChannelDefinition(channelId);
      if (foundDef) {
        console.warn('Already found a blog channel definition id:', foundDef);
        return;
      }

      const thereAndBackAgain: ChannelDefinition = {
        channelId: channelId,
        name: name,
        description: description,
        templateId: undefined,
      };

      saveChannel(thereAndBackAgain);

      return true;
    };

    await Promise.all(
      realmData.blog.channels.map(async (channelData) => {
        addChannel(channelData.id, channelData.name, channelData.description);
      })
    );
  };

  const createBlogMedia = async () => {
    const masterDrive = getBlogMasterContentTargetDrive();
    for (let i = 0; i < demoImageArray.length; i++) {
      await uploadMedia(client, demoImageArray[i], masterDrive);
    }
  };

  const createBlogDetailData = async () => {
    const imageFileIds = demoImageArray.map((data) => data.id);

    // Random blog post generation:
    for (let i = 0; i < 10; i++) {
      const randomTitle = `${getFunName()} tales`;
      const randomAbstract = getRandomAbstract();
      const randomFileId = rando(imageFileIds);

      const blogContent: BlogArticle = {
        id: DataUtil.getNewGuid(),
        channelId: client.blogDefinitionProvider.getDefaultChannelId().toString(),
        caption: randomTitle,
        dateUnixTime: 1658320178,
        primaryImageFileId: randomFileId,
        type: 'Article',
        readingTimeStats: {
          text: '2 minutes',
          time: 2,
          words: 382,
          minutes: 2,
        },
        abstract: randomAbstract,
        headerImageFileId: randomFileId,
        body: randomAbstract + ' and then some', //Note: this can be html
      };

      const blogFile: BlogPostFile<BlogContent> = {
        fileId: undefined,
        acl: {
          requiredSecurityGroup: SecurityGroupType.Anonymous,
        },
        publishTargets: [],
        content: blogContent,
      };

      const blogFileId = await client.blogPostProvider.saveBlogPostMaster(blogFile);

      const defaultChannelTarget: PublishTarget = {
        fileId: blogFileId.toString(),
        channelId: client.blogDefinitionProvider.getDefaultChannelId().toString(),
        acl: {
          requiredSecurityGroup: SecurityGroupType.Anonymous,
        },
        lastPublishTime: 1000,
      };

      const targets = [defaultChannelTarget];
      for (let i = 0; i < realmData.blog.channels.length; i++) {
        targets.push({ ...defaultChannelTarget, channelId: realmData.blog.channels[i].id });
      }

      await client.blogPostProvider.publishBlogPost(blogFile, targets);
    }

    // Summary
    const publishedPosts = await client.blogPostProvider.getPosts(
      BlogPostPublishStatus.Published,
      1,
      100
    );
    console.log(`Published Posts: ${publishedPosts.length}`, publishedPosts);
  };

  const createBlogData = async () => {
    await createBlogMedia();
    await createBlogDetailData();
  };

  return (
    <div className="mb-5">
      <h1>Home and theme:</h1>
      <button
        onClick={addChannels}
        className={`my-2 block w-1/3 rounded border-0  py-2 px-4 text-white hover:bg-green-600 focus:outline-none ${
          channelAttr && isChannelFetched ? 'pointer-events-none bg-gray-300' : 'bg-green-500'
        }`}
        disabled={channelAttr && isChannelFetched}
      >
        Create Channels
      </button>
      <button
        onClick={createBlogData}
        className="my-2 block w-1/3 rounded border-0 bg-green-300 py-2 px-4 text-white hover:bg-green-400 focus:outline-none"
      >
        Add blog posts
      </button>
    </div>
  );
};
